<template>
	<div class="tips">
		<div class="tips-content">
			<span class="icon"></span>
			<p>{{ tipText }}</p>
			<span class="iknow" @click="show = false">我知道了</span>
		</div>
	</div>
</template>

<script>
export default {
	props: {
		tipsShow: {
			type: Boolean,
			default: false
		},
		tipText: {
			type: String,
			default: ''
		}
	},
	computed: {
		show: {
			get() {
				return this.tipsShow;
			},
			set(val) {
				this.$emit('update:tipsShow', val);
			}
		}
	}
};
</script>

<style scoped lang="scss">
.tips {
	width: 100%;
	height: 100%;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 9999;
	background: rgba(0, 0, 0, 0.5);
	display: flex;
	justify-content: center;
	align-items: center;
	.tips-content {
		width: 472px;
		height: 280px;
		background: rgba(6, 31, 54, 0.85);
		border: 2px solid #1e7e81;
		box-shadow: 1px 10px 51px 0px #1f2535;
		border-radius: 10px;
		box-sizing: border-box;
		position: relative;
		transition: all 0.3s;
		text-align: center;
		span.icon {
			display: inline-block;
			width: 102px;
			height: 102px;
			background: url('../../assets/images/farmdetail/no-area.png') center;
			margin-top: 38px;
		}
		p {
			line-height: 65px;
			color: #00f6ff;
			font-size: 14px;
		}
		span.iknow {
			display: inline-block;
			width: 136px;
			height: 35px;
			line-height: 35px;
			text-align: center;
			border: 1px solid #2d8fa2;
			border-radius: 4px;
			font-size: 14px;
			color: #26c6da;
			cursor: pointer;
			transition: all 0.3s;
			&:hover {
				box-shadow: 0 2px 12px -2px rgba(2, 18, 33, 0.9);
			}
		}
	}
}
</style>



